import React, { useState, useEffect } from 'react'
import { 
  Grid,
  AppBar,
  Toolbar,
  Button,
  IconButton,
  Typography,
  makeStyles,
} from '@material-ui/core'
import { Menu as MenuIcon, ExitToApp as ExitIcon } from '@material-ui/icons'
import { Link } from 'react-router-dom';
import Loader from 'react-loader-spinner'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import ToolbarMenu from './ToolbarMenu'
import AddNote from './AddNote'
import Note from './Note'

import { getNotes } from '../requests'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  marginRight: {
    marginRight: theme.spacing(1),
  },
  title: {
    flexGrow: 1,
  },
}));

const HomePage = ({ history, setOpen, setData }) => {  
  const [isLoading, setIsLoading] = useState(false)
  const [notes, setNotes] = useState([])
  const [sortedNotes, setSortedNotes] = useState([])

  useEffect(() => {
    localStorage.getItem('authData') && getNotes(setIsLoading, data => {
      setNotes(data)
      setSortedNotes(data)
    })
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const classes = useStyles();
  return (
    <>
      <AppBar position="static" style={{ height: '50px' }}>
        <Toolbar style={{ minHeight: '100%' }}>
          <IconButton edge="start" className={classes.marginRight} color="inherit">
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Notes
          </Typography>
          {localStorage.getItem('authData') ?
          <>
            <Typography  className={classes.marginRight}>
              {JSON.parse(localStorage.getItem('authData')).user_name}
            </Typography>
            <IconButton onClick={() => {
              if(window.confirm("Выйти из аккаунта?")){
                localStorage.removeItem('authData');
                history.push('/login');
              }
            }}>
              <ExitIcon />
            </IconButton>
          </>
          : 
          <>
            <Button color="inherit" component={Link} to='/login'>Login</Button>
            <Button color="inherit" component={Link} to='/signup'>Register</Button>
          </>
          }
        </Toolbar>
      </AppBar>
      {localStorage.getItem('authData') ?  
      <div style={{ padding: 30 }}>
        <ToolbarMenu notes={notes} setSortedNotes={setSortedNotes} />
        <Grid container spacing={1} className={isLoading ? 'home-center' : ''}>
          {isLoading ? <Loader type="CradleLoader" color="#64ba69" height={80} width={80} />
          : sortedNotes.map(note => <Note key={note.id} note={note} setOpen={setOpen} setData={setData} setNotes={setNotes} />)
          .concat(<AddNote key={'addnote'} setOpen={setOpen} setData={setData} setNotes={setNotes} />)}
        </Grid>
      </div>
      :
      <div>Login</div>
      }
    </>
  )
}

export default HomePage
